@mixin utilities {
    &-container {
        max-width: 1200px;
        margin: 0 auto;
    }

    &-text {
        &-size {
            &--small {
                font-size: 0.75rem;
            }
        }

        &-align {
            &--center {
                text-align: center;
            }
            
            &--left {
                text-align: left;
            }
            
            &--right {
                text-align: right;
            }
        }

        &-transform {
            &--capitalize {
                text-transform: capitalize;
            }

            &--uppercase {
                text-transform: uppercase;
            }

            &--lowercase {
                text-transform: lowercase;
            }
        }

        &-heading {
            display: block;
            font-family: var(--kb-font-family-display);
            font-weight: 400;
            line-height: 1.25;
            margin: 1rem 0;
        
            &--small {
                font-size: 1.5rem;

                @media #{$kb-mq-x-small} {
                    font-size: 1.25rem;
                }
            }
        
            &--medium {
                font-size: 2.5rem;

                @media #{$kb-mq-medium} {
                    font-size: 2rem;
                }

                @media #{$kb-mq-x-small} {
                    font-size: 1.75rem;
                }
            }
        
            &--large {
                font-size: 3.5rem;

                @media #{$kb-mq-medium} {
                    font-size: 2.5rem;
                }

                @media #{$kb-mq-x-small} {
                    font-size: 2rem;
                }
            }
        }
        
        &-color {
            &--brand {
                color: var(--kb-color-brand);
            }
            
            &--regular {
                color: var(--kb-color-text);
            }
        }

        &--assistive {
            position: absolute !important;
            margin: -1px !important;
            border: 0 !important;
            padding: 0 !important;
            width: 1px !important;
            height: 1px !important;
            overflow: hidden !important;
            clip: rect(0 0 0 0) !important;
            text-transform: none !important;
            white-space: nowrap !important;
        }
    }

    &-opacity {
        &--0 {
            opacity: 0%;
        }
        
        &--25 {
            opacity: 25%;
        }

        &--50 {
            opacity: 50%;
        }

        &--75 {
            opacity: 75%;
        }

        &--100 {
            opacity: 100%;
        }
    }

    &-position {
        &--relative {
            position: relative;
        }

        &--sticky {
            position: sticky;
        }

        &--absolute {
            position: absolute;
        }

        &--fixed {
            position: fixed;
        }
    }

    &-list {
        &--horizontal {
            list-style: none;
            padding: 0;
            margin: 0;
            display: flex;

            > li {
                flex: 0 0 auto;

                &:not(:last-child)::after {
                    content: 'â€¢';
                    display: inline-block;
                    font-size: 0.75rem;
                    margin: 0 0.5rem;
                }
            }
        }
    }

